@use "../bootstrap/bootstrap.scss";

.advantagesList {
  @include bootstrap.listReset;

  @media (min-width: bootstrap.$desktop) {
    display: flex;
    gap: 38px;
  }

  &__item {
    display: flex;
    gap: 16px;
    align-items: center;
    @media (min-width: bootstrap.$desktop) {
      flex: 1;
      flex-direction: column;
      align-items: center;
    }

    & + & {
      margin-top: 24px;
      @media (min-width: bootstrap.$desktop) {
        margin-top: 0;
      }
    }

    &::before {
      content: "";
      display: block;
      width: 64px;
      height: 64px;
      top: 0;
      left: 0;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;

      @media (min-width: bootstrap.$desktop) {
        width: 72px;
        height: 72px;
      }
    }

    &:nth-child(1)::before {
      background-image: url("../../assets/images/check.svg");
    }

    &:nth-child(2)::before {
      background-image: url("../../assets/images/modern-house.svg");
    }

    &:nth-child(3)::before {
      background-image: url("../../assets/images/savings.svg");
    }

    &:nth-child(4)::before {
      background-image: url("../../assets/images/rating.svg");
    }
  }

  &__text {
    margin: 0;
    position: relative;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.5;
    color: #263238;
    flex: 1;

    @media (min-width: bootstrap.$desktop) {
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      text-align: center;
      padding-left: 8px;
      padding-right: 8px;
    }

    .advantagesList__item:nth-child(4) & {
      max-width: 134px;
    }

    & svg {
      display: inline-block;
      vertical-align: baseline;
    }
  }
}
